import { useState } from "react";
import { useDispatch } from "react-redux";
import { Container, Form, Button, Alert } from "react-bootstrap";
import { createUser } from "./actions/users";
function App() {
  const [postData, setPostData] = useState({
    name: "",
    email: "",
    number: "",
    date: "",
  });
  const [success, setSuccess] = useState(false);
  function handleSubmit(e) {
    e.preventDefault();

    dispatch(createUser(postData));
    setSuccess(true);
    setTimeout(() => {
      setSuccess(false);
    }, 2000);
  }
  const dispatch = useDispatch();
  return (
    <div
      style={{
        width: "100vw",
        height: "100vh",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <Container className="d-flex justify-content-center">
        <Form className="mt-4 w-50" onSubmit={handleSubmit}>
          <div className="d-flex justify-content-center mb-4">
            <Form.Label className="mb-4 fs-3 fw-bold ">
              Fill the form to book an appointment
            </Form.Label>
          </div>
          {success && (
            <Alert variant="success">
              <Alert.Heading>Successfully Booked Appointment</Alert.Heading>
            </Alert>
          )}
          <Form.Group className="mb-4" controlId="formBasicEmail">
            <Form.Label>Name</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter name"
              required
              onChange={(e) =>
                setPostData({ ...postData, name: e.target.value })
              }
            />
          </Form.Group>
          <Form.Group className="mb-4" controlId="formBasicEmail">
            <Form.Label>Email address</Form.Label>
            <Form.Control
              type="email"
              placeholder="Enter email"
              onChange={(e) =>
                setPostData({ ...postData, email: e.target.value })
              }
              required
            />
          </Form.Group>

          <Form.Group className="mb-4" controlId="formBasicPassword">
            <Form.Label>Phone Number</Form.Label>
            <Form.Control
              type="number"
              placeholder="+91"
              required
              onChange={(e) =>
                setPostData({ ...postData, number: e.target.value })
              }
            />
          </Form.Group>
          <Form.Group className="mb-4" controlId="formBasicEmail">
            <Form.Label>Date of Appointment</Form.Label>
            <Form.Control
              type="date"
              required
              onChange={(e) =>
                setPostData({ ...postData, date: e.target.value })
              }
            />
          </Form.Group>
          <div className="d-flex justify-content-center">
            <Button variant="primary" type="submit" className="mt-4">
              Submit
            </Button>
          </div>
        </Form>
      </Container>
    </div>
  );
}

export default App;
